import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { SpacesBaseService } from '../../spaces_base.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'spaces-landing-page',
  template: `<div *ngIf="!done" class="centered">
  <h4>Checking parameters</h4>
</div>

<div *ngIf="done && missingParams.length > 0" class="centered">
  <h4>You must configure parameters to use this app.</h4>
</div>
`,
  styles: [`.centered{display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-pack:distribute;justify-content:space-around;margin-top:150px}`]
})
export class SpacesLandingPageComponent implements OnInit {

  missingParams: any[] = [];
  done = false;
  constructor(private http: HttpClient, private spacesBaseService: SpacesBaseService, private router: Router) { }

  public ngOnInit() {
    this.spacesBaseService.initialized.then(() => {
      const params = this.spacesBaseService.params;

      if (params.develop === 'true') {
        this.done = true;
        this.router.navigate(['app']);
        return;
      }
      this.http.get('install.json', {responseType: 'json'}).subscribe(
        installJson => {

          this.missingParams =
            installJson['params']
              .filter(param => param.required)
              .filter(param => !params[param.name]);

          this.done = true;

          if (this.missingParams.length === 0) {
            this.router.navigate(['app']);
          }
        },
        err => {
          console.log(err);
        });
    });
  }

}
